.card{
    border-radius: 52px;
    padding: 22px 22px;
    border: none;
}

.card-img{
    width: 100px;
    height: 100px;
    border-radius: 20px;
    background:#FFE8C8;
}
.home-img{
    padding: 24px 24px;
    
}
.shadow{
    box-shadow: 15px 15px;
}

.button-l{
    width: 170px;
    height: 49px;
    border-radius: 100px;
    border: none;
}


@media (min-width: 991px) and (max-width: 1400px){
    .enjoy-text {
        font-size: 30px;
        line-height: 52.2px;
      }

      .menu-text {
        font-size: 16px;
        line-height: 0px;
       }
       .button-l {
        width: 142px;
      }
}
@media (min-width: 576px) and (max-width: 991px){
    .enjoy-text {
        font-size: 30px;
        line-height: 52.2px;
      }

      .menu-text {
        font-size: 16px;
        line-height: 0px;
      }
      .button-l {
        width: 142px;
      }
}