.pic{
  position: relative;
  margin-bottom: 10px;
  text-align: center;
  overflow: hidden; 
}
.text-content{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 0 0 0 93px;
 }
.pic > img,
.pic > .text-content{
  transition: 200ms ease-in-out;
}
.pic > .text-content.fade{
  opacity: 0;
}
.pic:hover > .text-content .fade {
  opacity:1;
}
.pic:hover > img.zoom {
  filter: opacity(0.8);
}









