.subscribe{
    background:#FFF8ED url("/src/assets/images/subscribe.png") no-repeat;
    background-position: center 103px;
}

.input-email{
    width:700px;
    height: 80px;
    border-radius: 40px;
    border: none;
    padding: 0 0 0 50px;
}

.sbtn{
    width: 170px;
    height: 64px;
    border-radius: 100px;
    margin: 8px 0 0 -183px;
    border: none;
}

input[type="text"]:focus {
    outline: none;
 }
 @media (min-width: 240px) and (max-width: 576px){
    .input-email {
        width: 219px;
        height: 47px;
        padding: 0 0 0 9px;
      }
      .sbtn {
        width: 107px;
        height: 39px;
        margin: 4px 0 0 -50px;
      
      }

      .sub-text-input {
        font-size: 12px;
      }
 }
 